import React,{useState, useContext}  from 'react'
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button'; 
import {  Link} from 'react-router-dom';
import axios from 'axios';
import toast  from 'react-hot-toast'; 
import { useNavigate  } from 'react-router-dom';
import { userContext } from '../contex/userContext';

const Login = () => {
  const {auth, setAuth} = useContext(userContext);
  const [userData, setuserData] = useState({email:"",password:"" });
  const navigate = useNavigate();
   
  const handleSubmit = async () => { 
    try {
      const {data} = await axios({
        method: 'post',
        url: `${process.env.REACT_APP_SERVERURL}/api/auth/login`,
        data:  {  
         email: userData.email,
         password: userData.password 
        }
      })
      // if data.success = true 
      if(data.success){
        toast.success(data.message);  
        const auth = {user: data.user, token: data.token };
        localStorage.setItem('auth', JSON.stringify(auth) );  
        setAuth(JSON.parse(localStorage.getItem('auth')))     
        navigate('/');
      }
      // if data.success = false 
      else{
        toast.error(data.message);
      }

    } catch (error) {
      console.log(error); 
    }     
  }

  const handleChange = (event) => {
    setuserData({...userData, [event.target.name]: event.target.value });  
  }
    

  return (
    <div>
        <Form>
          <Form.Group className="mb-3" controlId="formBasicEmail">
            <Form.Label>Email address</Form.Label>
            <Form.Control type="email" placeholder="Enter email" autoComplete='on' 
            name='email' value={userData.email} onChange={(e) => handleChange(e) }  /> 
          </Form.Group>          

          <Form.Group className="mb-3" controlId="formBasicPassword">
            <Form.Label>Password</Form.Label>
            <Form.Control type="password" placeholder="Password"  autoComplete='on' name='password' value={userData.password} onChange={(e) => handleChange(e) }   />
          </Form.Group> 
        
          <Link to='/ForgotPassword'>
            <Button variant="primary" type="button"  > Forgot Password </Button>
          </Link> 

          <Button variant="primary" type="button" className='w-25 mx-auto'
          onClick={()=> handleSubmit()}  >
             Login
          </Button>
          
        </Form>

    </div>
  )
}

export default Login
