import React, { useContext, useEffect, useState } from 'react'
import axios from 'axios';
import { toast } from 'react-hot-toast';
import { Link } from 'react-router-dom'
import { userContext } from '../contex/userContext';
import AdminMenu from './AdminMenu'

const Products = () => {
    const [products, setProducts] = useState([]);
    const { auth } = useContext(userContext);

    const getallProducts = async () => {
        const { data } = await axios({
            method: 'get',
            url: `${process.env.REACT_APP_SERVERURL}/api/products/all-products`,
            headers: {
                'auth-token': auth?.token
            }
        })
        if (data.success) {
            setProducts(data.products)
            console.log(data);
        } else {
            toast.error(data.message)
        }
    }


    useEffect(() => {
        getallProducts();
    }, [])

    return (
        <div className='container mt-5'>
            <div className="row gap-3"> 

                <div className="col col-3">
                    <AdminMenu />
                </div>
                <div className="col col-8 ">
                    <h1>All Products</h1>
                    <div className="d-flex flex-wrap gap-4 mt-4">

                        {
                            products ? products.map((product) => {
                                return <Link to={`/Dashboard/SingleProduct/${product.slug}`} key={product._id}>
                                    <div className="card p-1" >
                                        <img src={`${process.env.REACT_APP_SERVERURL}/api/products/product-photo/${product._id}`} className="card-img-top mx-auto" alt={product.name}
                                            style={{ "width": "6rem", "height": "6rem" }} />
                                        <div className="card-body">
                                            <h5 className="card-title">{product.name}</h5>
                                            <p className="card-text">
                                                {product.description} </p>
                                            <Link className="btn btn-primary me-1" > 
                                            Details </Link>
                                            <Link className="btn btn-secondary" >Add to Cart  </Link>
                                        </div>
                                    </div>
                                </Link>

                            }) : <h1>No Products Available</h1>
                        }
                    </div>

                </div>

            </div >

        </div >
    )

}


export default Products
