import React, { useEffect, useState, useContext }  from 'react'
import UserMenu from './UserMenu' 
import Button from 'react-bootstrap/Button';  
import Form from 'react-bootstrap/Form';  
import axios from 'axios';
import toast  from 'react-hot-toast'; 
import { userContext } from '../contex/userContext';

const Profile = () => {
  const {auth, setAuth} = useContext(userContext);

  const [userData, setuserData] = useState({name: "",email:"",password:"",phoneNo:"",address:""}); 

  useEffect(() => {
    setuserData(auth?.user)
  },[])
   
  const handleSubmit = async () => { 
    try {
      const {data} = await axios({
        method: 'put',
        url: `${process.env.REACT_APP_SERVERURL}/api/auth/profile`,
        data:  { 
         name:  userData.name, 
         email: userData.email,
         password: userData.password,
         phoneNo: userData.phoneNo, 
         address: userData.address  
        },
        headers:{
          'auth-token':  auth?.token
        }
      })
      // if data.success = true 
      if(data.success){ 
        setAuth({...auth, user : data.updatedUser})
        
        let authLs = JSON.parse(localStorage.getItem('auth'))
        authLs.user = data.updatedUser
        localStorage.setItem('auth', JSON.stringify(authLs) )
        toast.success(data.message);     
      }
      // if data.success = false 
      else{
        toast.error(data.message, {
          duration: 4000,
          position: 'top-center'} );
      }

    } catch (error) {
      console.log(error); 
    }     
  }

  const handleChange = (event) => {
    setuserData({...userData, [event.target.name]: event.target.value });  
  }

  return (
    <div className='container mt-5'>
    <div className="row d-flex gap-3 ">

      <div className="col col-3">
        <UserMenu />
      </div>

      <div className="col col-8" >
          <h1>Profile</h1>
     
        <Form>
          <Form.Group className="mb-3" controlId="formBasicName">
            <Form.Label>Name</Form.Label>
            <Form.Control type="text" placeholder="Name"  autoComplete='on' name='name'
            value={userData.name} onChange={(e) => handleChange(e) } />
          </Form.Group> 

          <Form.Group className="mb-3" controlId="formBasicEmail">
            <Form.Label>Email </Form.Label>
            <Form.Control type="email" placeholder="Enter email" autoComplete='on' name='email' value={userData.email} onChange={(e) => handleChange(e) } disabled/>
          </Form.Group>

          <Form.Group className="mb-3" controlId="formBasicPassword">
            <Form.Label>Password</Form.Label>
            <Form.Control type="password" placeholder="Password" name='password' autoComplete='on'  
            value={userData.password} onChange={(e) => handleChange(e) }  />
          </Form.Group> 

          <Form.Group className="mb-3" controlId="formBasicNumber">
            <Form.Label>Phone No</Form.Label>
            <Form.Control type='tel'  placeholder="Phone No" name='phoneNo'  autoComplete='on' 
            value={userData.phoneNo} onChange={(e) => handleChange(e) }    />
          </Form.Group>  

          <Form.Group className="mb-3" controlId="formBasicAddress">
            <Form.Label>Address</Form.Label>
            <Form.Control type="Address" placeholder="Address" name='address' autoComplete='on'
            value={userData.address} onChange={(e) => handleChange(e) }   />
          </Form.Group> 

          <Button variant="primary" type="button"  onClick={()=> handleSubmit()} >
             Update
          </Button>
        </Form>
      </div>

    </div>
  </div> 
  )
}

export default Profile
