import { useContext } from 'react';
import { toast } from 'react-hot-toast';
import { Link, useNavigate } from 'react-router-dom';
import { userContext } from '../contex/userContext';
import { useCart } from '../contex/cartContex';
import useCategory from '../hooks/useCategory';
import SearchInput from './SearchInput';


const Header = () => {
  const { auth, setAuth } = useContext(userContext); 
  const {cart} = useCart();
  const categories = useCategory(); 

  const logoutSystem = () => {
    localStorage.removeItem('auth');
    setAuth({ user: null, token: "" })
    toast('Logout Successfully');
  }

  return (

    <nav className="navbar navbar-expand-lg bg-dark text-light " style={{height: "6rem"}}>
      <div className="container">
        <Link className="navbar-brand  text-light" to="/">Ecommerce</Link>
        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav ms-auto my-1">
            <li className="nav-item">
              <Link className="nav-link  text-light" to="/">Home</Link>
            </li>
            <li className="nav-item dropdown">
              <Link className='nav-link dropdown-toggle text-light text-capitalize' role="button" data-bs-toggle="dropdown" aria-expanded='false' > Categories  </Link>
              <ul className='dropdown-menu bg-dark'>
               {
                  categories?.map((category) => {
                    return <li className="nav-item dropdown-item">  <Link 
                    className="nav-link text-light text-capitalize" 
                    to={`/Category/${category.slug}`}>{category.name} </Link> </li> 
                  })
               }    
              </ul>
            </li>
            <li className="nav-item">
              <Link className="nav-link text-light cart" to="/Cart" >
                Cart {cart.length > 0 ? <p className='badge'>{cart.length} </p> : "" } </Link>
            </li>

            {
              auth.user ?
                <li className="nav-item dropdown">
                  <Link className='nav-link dropdown-toggle text-light text-capitalize' role="button" data-bs-toggle="dropdown" aria-expanded='false' >
                    {auth.user.role === 0 ? auth.user.name : 'Admin'} </Link>
                  <ul className='dropdown-menu bg-dark'>
                    <li className="nav-item dropdown-item"  >  <Link className="nav-link nav-item text-light"
                      to={`/Dashboard/${auth.user.role === 0 ? 'user' : 'admin'} `} > Dashboard</Link> </li>
                    <li className="nav-item dropdown-item">  <Link className="nav-link text-light" onClick={() => logoutSystem()}> Logout</Link> </li>
                  </ul>
                </li>
                :
                <>
                  <li className="nav-item">
                    <Link className="nav-link text-light" to="/SignUp">SignUp</Link>
                  </li>
                  <li className="nav-item">
                    <Link className="nav-link text-light" to="/Login">Login </Link>
                  </li>
                </>
            }

            <SearchInput />

          </ul>
        </div>
      </div>
    </nav>

  )
}

export default Header;



