import React from 'react'

const ProductForm = (props) => {
    const {category, setCategory, handleSubmit} = props;

    return (
        <div>
            <form className='m-0 gap-1 mb-5' onSubmit={(e) => handleSubmit(e) }>
                <div className="mb-3"> 
                    <input type="text" className="form-control" id="exampleInputName"
                    placeholder='Enter Category...' 
                    onChange={(e) => setCategory(e.target.value ) } /> 
                </div>

                <button type="submit" className="btn btn-primary w-50" >Add Category</button>
            </form>

        </div>
    )
}

export default ProductForm
