import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import axios from 'axios';
import { toast } from 'react-hot-toast';
import { Link } from 'react-router-dom'

const Search = () => {
    const {keyword} = useParams();
    console.log(keyword);
    const [products, setProducts] = useState([]);

    const searchProducts = async () => {
        const { data } = await axios({
            method: 'get',
            url: `${process.env.REACT_APP_SERVERURL}/api/products/products-search/${keyword}`,
            headers: {
                'auth-token': JSON.parse(localStorage.getItem('auth')).token
            }
        })
       setProducts(data)  
    }

    useEffect(() => {
        searchProducts();
    },[keyword])

  return (
    <div className='container mt-5'>
    <div className="row gap-3">  

        <div className="col"> 
            <h3> {products.length < 1 ?" No Search Result Found " : `Found ${products.length} Matching Results` }  </h3>
            <div className="d-flex flex-wrap gap-4 mt-4">

                {
                    products?.map((product) => {
                        return <Link to={`/Dashboard/SingleProduct/${product.slug}`} key={product._id}>
                            <div className="card p-1" >
                                <img src={`${process.env.REACT_APP_SERVERURL}/api/products/product-photo/${product._id}`} className="card-img-top mx-auto" alt={product.name}
                                    style={{ "width": "6rem", "height": "6rem" }} />
                                <div className="card-body">
                                    <h5 className="card-title">{product.name}</h5>
                                    <p className="card-text">
                                        {product.description} </p>
                                    <Link className="btn btn-primary me-1" > 
                                    Details </Link>
                                    <Link className="btn btn-secondary" >Add to Cart  </Link>
                                </div>
                            </div>
                        </Link>

                    }) 
                }
            </div>

        </div>

    </div >

</div >
  )
}

export default Search
