import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { toast } from 'react-hot-toast';  
import moment from 'moment'
import AdminMenu from './AdminMenu';

const AdminOrders = () => { 
    const status = ["Not Process", "Processing","Shipping","Delivered","Cancel" ]
    const [selectStatus, setSelectStatus ] = useState("");

    const [orders, setOrders] = useState([]);
    
    const getAllOrders = async () => {
        const { data } = await axios({
        method: 'get',
        url: `${process.env.REACT_APP_SERVERURL}/api/paymentIntegration/all-orders`,
        headers: {
            'auth-token': JSON.parse(localStorage.getItem('auth')).token
        }
        })
        if (data.success) {
        console.log(data.orders);
        setOrders(data.orders)
        } else {
        toast.error(data.message)
        }
    }

    const  updateStatus = async (statusvalue, id) => {
        const { data } = await axios({
        method: 'put',
        url: `${process.env.REACT_APP_SERVERURL}/api/paymentIntegration/order-status/${id}`,
        data: {
            status: statusvalue
        },
        headers: {
            'auth-token': JSON.parse(localStorage.getItem('auth')).token
        }
        })
        if (data.success) { 
            // No such need to call it because if page render it is already called in useEfflect 
            // getAllOrders();   
        } else {
         toast.error(data.message)
         console.log(data.error);
        }
    }
    
    useEffect(() => {
        getAllOrders();
    }, [])

    // console.log(selectStatus);

    return (
        <div className='container mt-5'>
           <div className="row d-flex gap-3">
            <div className="col col-3">
                <AdminMenu />
            </div>

            <div className="col col-8" >
                <h1>All Orders</h1> 
             {orders?.map((order, i) => {
                return (
                 <div className='mt-5'>
                    <table class="table" key={order._id}>
                        <thead>
                            <tr>
                                <th scope="col">#</th>
                                <th scope="col"> Status </th>
                                <th scope="col">Buyer</th>
                                <th scope="col"> Date</th>
                                <th scope="col">Payment</th>
                                <th scope="col"> Quantity</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <th scope="row">{i + 1}</th>
                                <td>  
                                    <select name="status" id="status" defaultValue={order?.status} onChange={(e) => updateStatus(e.target.value, order._id)} >
                                        {
                                            status.map((s, index) => {
                                                return <option value={s} key={index}>{s}</option>
                                            })
                                        }
                                    </select>
                                </td>
                                <td className='text-capitalize'> {order.buyer.name}</td>
                                <td> {moment(order.createdAt).fromNow()}</td>
                                <td> {order.payment.success ? "Success" : "Failed"}</td>
                                <td> {order.products.length}</td>
                            </tr>
                        </tbody>
                    </table>

                    <div className="col">
                        {
                            order?.products?.map((product) => {
                                return <div className="card p-1" key={product._id}>
                                    <img src={`${process.env.REACT_APP_SERVERURL}/api/products/product-photo/${product._id}`} className="card-img-top mx-auto" alt={product.name}
                                        style={{ "width": "6rem", "height": "6rem" }} />
                                    <div className="card-body">
                                        <h5 className="card-title">Prouduct: {product.name}</h5>
                                        <p className="card-text">
                                            Description:   {product.description}</p>
                                        <p>Price : {product.price}</p>
                                    </div>
                                </div>
                            })
                        }

                    </div>
                </div>
                    )
                })
                }




            </div>

            </div>
        </div>
    )
}

export default AdminOrders
