import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import axios from 'axios';
import { toast } from 'react-hot-toast';
import { Link } from 'react-router-dom'
import { useCart } from '../contex/cartContex'

const Category = () => {
  const { cart, setCart } = useCart();

  const { slug } = useParams();
  const [category, setCategory] = useState({});
  const [products, setproducts] = useState([]);

  const productsbyCategory = async () => {
    const { data } = await axios({
      method: 'get',
      url: `${process.env.REACT_APP_SERVERURL}/api/products/oneCategory-products/${slug}`,
      headers: {
        'auth-token': JSON.parse(localStorage.getItem('auth')).token
      }
    })
    if (data.success) {
      setCategory(data.category)
      setproducts(data.products)
    } else {
      toast.error(data.message)
    }
  }

  useEffect(() => {
    productsbyCategory();
  })

  return (
    <div className='container'> 
      <div className='row'>
        <h2 className='my-3 d-flex ' style={{alignItems: "baseline"}} >Category:   <h4 className='mb-0 ms-3'>{category.name}</h4>  </h2>

        <div className="col"> 
          <div className="d-flex flex-wrap gap-4 mt-4">
            {
              products?.map((product) => {
                return <div className="card p-1" key={product._id}>
                  <img src={`${process.env.REACT_APP_SERVERURL}/api/products/product-photo/${product._id}`} className="card-img-top mx-auto" alt={product.name}
                    style={{ "width": "6rem", "height": "6rem" }} />
                  <div className="card-body">
                    <h5 className="card-title">{product.name}</h5>
                    <p className="card-text">
                      {product.description.substring(0, 15)}... </p>
                    <Link className="btn btn-primary me-1" to={`/ProductDetails/${product.slug}`}>
                      Details </Link>
                    <Link className="btn btn-secondary" 
                     onClick={() => {
                      setCart([...cart, product])
                      localStorage.setItem('cart', JSON.stringify([...cart, product]))
                      toast.success('Item added to Cart')
                    }} > Add to Cart  </Link>
                  </div>
                </div>
              })
            }
          </div>
        </div>


      </div>
    </div>
  )
}

export default Category
