import React, { useContext, useEffect, useRef, useState } from 'react'
import axios from 'axios';
import AdminMenu from './AdminMenu'
import { userContext } from '../contex/userContext';
import { toast } from 'react-hot-toast';
import ProductForm from '../components/ProductForm'; 

const CreateCategory = () => {
  const [categories, setCategories] = useState([]);
  const { auth } = useContext(userContext);

  const getCategories = async () => {
    const { data } = await axios({
      method: 'get',
      url: `${process.env.REACT_APP_SERVERURL}/api/category/all-category`,
      headers: {
        'auth-token': auth?.token
      }
    })
    if (data.success) {
      setCategories(data.categories)
    } else {
      toast.error(data.message)
    }
  }

  useEffect(() => {
    getCategories();
  }, [])

  const deletecategory = async (id) => {
    const { data } = await axios({
      method: 'delete',
      url: `${process.env.REACT_APP_SERVERURL}/api/category/delete-category/${id}`,
      headers: {
        'auth-token': auth?.token
      }
    })

    if (data.success) {
      getCategories();
    } else {
      toast.error(data.message)
    }
  }

  // Add Category
  const [category, setCategory] = useState("");

  const handleSubmit = async (e) => {
    e.preventDefault();
    const { data } = await axios({
      method: 'post',
      url: `${process.env.REACT_APP_SERVERURL}/api/category/create-category`,
      data: {
        name: category
      },
      headers: {
        'auth-token': auth?.token
      }
    })
    if (data.success) {
      toast.success(data.message) 
      getCategories();
    } else {
      toast.error(data.message)
    }
  }

  // Update Category
  const [updatedCategory, setUpdatedCategory] = useState({name:""});
  const [selectedCategory, setSelectedCategory] = useState({name:""});
  const refopen = useRef();
  const refclose = useRef(null);

  const updateCategory = async (id) => {  
    setSelectedCategory({name:""})
    const editcategory = categories.filter((category) => {
      return category._id === id;
    })
    setUpdatedCategory(editcategory[0]); 
    refopen.current.click();
  }

  const handleCategoryupdate = (e) => {
    setSelectedCategory(
      {...selectedCategory, 
        [e.target.name]: e.target.value, 
      }); 
  }

  const submitUpdatedCategory = async () => { 
    refopen.current.click();  
     
    let targetcategory = categories.filter((category) => {
      return category.name === updatedCategory.name;
    }) 
    const id = targetcategory[0]._id; 
     
     const { data } = await axios({
      method: 'put',
      url: `${process.env.REACT_APP_SERVERURL}/api/category/update-category/${id}`,
      data: {
         name: selectedCategory.name  
      },
      headers: {
        'auth-token': auth?.token
      }
    })
    if (data.success) {  
      toast.success(data.message)
      console.log(data); 
      getCategories();
      setSelectedCategory({name:""}) 
    } else {
      toast.error(data.message)
    }
  }


  return (

    <div className='container mt-5'>
      <div className="row gap-3">

        <div className="col col-3">
          <AdminMenu />
        </div>
        <div className="col col-8 ">
          <ProductForm category={category} setCategory={setCategory}
            handleSubmit={handleSubmit} />

          <table className="table">
            <thead>
              <tr>
                <th scope="col">Category Name</th>
                <th scope="col" colSpan={2}> Actions</th>
              </tr>
            </thead>
            {
              categories ? categories.map((category, index) => {
                return <tbody className="table-group-divider" key={category._id}>
                  <tr>
                    <td className='text-capitalize'> {category.name} </td>

                    <td>
                      <button className='btn btn-primary'
                        onClick={() => updateCategory(category._id)} >Edit </button>
                    </td>

                    <td> <button className='btn btn-danger' onClick={() => deletecategory(category._id)}> Delete </button> </td>
                  </tr>
                </tbody>
              })
                : <h3>No Categories Available</h3>
            }
          </table>
        </div>

        <div>
          {/* Button trigger modal */}
          <button type="button" className="btn btn-primary" ref={refopen}
            style={{ 'display': 'none' }} data-bs-toggle="modal" data-bs-target="#exampleModal">
            Launch demo modal
          </button>
          {/* Modal */}
          <div className="modal fade" id="exampleModal" tabIndex={-1} aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div className="modal-dialog">
              <div className="modal-content">
                <div className="modal-header">
                  <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" />
                </div>
                <div className="modal-body">
                  <input type="text" className="form-control" name='name'
                    defaultValue={updatedCategory?.name}
                    onChange={(e) => handleCategoryupdate(e) } /> 
                </div>
                <div className="modal-footer">
                  <button type="button" className="btn btn-secondary" ref={refclose} data-bs-dismiss="modal">Close</button>
                  <button type="button" className="btn btn-primary"                    
                    disabled={!selectedCategory.name  ? true : false } 
                    onClick={() => submitUpdatedCategory()} > Update </button>
                </div>
              </div>
            </div>
          </div>
        </div>


      </div>
    </div>

  )
}

export default CreateCategory
