import React, {useState} from 'react'
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';  
import axios from 'axios';
import toast  from 'react-hot-toast'; 
import { useNavigate  } from 'react-router-dom';

const ForgotPassword = () => {
  const [userData, setuserData] = useState({email:"",password:"",answer:""});
  const navigate = useNavigate();
   
  const handleSubmit = async () => { 
    try {
      const {data} = await axios({
        method: 'put',
        url: `${process.env.REACT_APP_SERVERURL}/api/auth/forgotPassword`,
        data:  {   
         email: userData.email,
         password: userData.password,
         answer: userData.answer 
        }
      })
      // if data.success = true 
      if(data.success){
        toast.success(data.message, {
          duration: 4000,
          position: 'top-center'} );

        navigate('/login')        
      }
      // if data.success = false 
      else{
        toast.error(data.message, {
          duration: 4000,
          position: 'top-center'} );
      }

    } catch (error) {
      console.log(error); 
      toast.error('Some error occured, Please try again')
    }     
  }

  const handleChange = (event) => {
    setuserData({...userData, [event.target.name]: event.target.value });  
  }
  return (
    <div>
       <Form>
          <Form.Group className="mb-3" controlId="formBasicEmail">
            <Form.Label>Email</Form.Label>
            <Form.Control type="email" placeholder="Enter email" autoComplete='on' 
            name='email' value={userData.email} onChange={(e) => handleChange(e) }  />
          </Form.Group>          

          <Form.Group className="mb-3" controlId="formBasicAnswer">
            <Form.Label>What is your favourite Sports ?</Form.Label>
            <Form.Control type="text" placeholder="Favourite Sports"  autoComplete='on' name='answer' value={userData.answer} onChange={(e) => handleChange(e) }   />
          </Form.Group> 
          
          <Form.Group className="mb-3" controlId="formBasicPassword">
            <Form.Label>New Password</Form.Label>
            <Form.Control type="password" placeholder="Password"  autoComplete='on' name='password'
            value={userData.password} onChange={(e) => handleChange(e) }   />
          </Form.Group>  

          <Button variant="primary" type="button" onClick={()=> handleSubmit()}  >
             Reset Password
          </Button>
          
        </Form>
    </div>
  )
}

export default ForgotPassword
