import React, { useContext, useEffect, useState } from 'react'
import { useCart } from '../contex/cartContex'
import { userContext } from '../contex/userContext'
import { useNavigate } from 'react-router-dom'
import DropIn from "braintree-web-drop-in-react";
import { toast } from 'react-hot-toast'
import axios from 'axios'

const Cart = () => {
  const { cart, setCart } = useCart();
  const { auth } = useContext(userContext);
  const navigate = useNavigate();

  const [order, setOrder] = useState([]);
  const [clientToken, setClientToken] = useState("");
  const [instance, setInstance] = useState("")

  const removeCartItem = (id) => {
    let myCart = [...cart]
    myCart = myCart.filter((product) => product._id !== id);
    setCart(myCart);
    localStorage.setItem('cart', JSON.stringify(myCart));
  }

  const totalPrice = () => {
    let totalCart = [...cart]
    const val = totalCart.reduce((total, currproduct) => {
      return total + currproduct.price
    }, 0)
    return val.toLocaleString('en-US', {
      style: 'currency',
      currency: 'USD'
    });
  }

  const getClientToken = async () => {
    try {
      const { data } = await axios({
        method: 'get',
        url: `${process.env.REACT_APP_SERVERURL}/api/paymentIntegration/Braintree-token`,
        headers: {
          'auth-token': JSON.parse(localStorage.getItem('auth')).token
        }
      })
      console.log(data);
      setClientToken(data?.clientToken)

    } catch (error) {
      console.log(error);
    }
  }

  const handlePayment = async () => {
    const { nonce } = await instance.requestPaymentMethod()

    const { data } = await axios({
      method: 'post',
      url: `${process.env.REACT_APP_SERVERURL}/api/paymentIntegration/Braintree-payment`,
      data: {
        cart, nonce
      },
      headers: {
        'auth-token': JSON.parse(localStorage.getItem('auth')).token
      }
    })
    if (data.success) {
      toast.success("Payment Completed Sucessfully")
      localStorage.removeItem('cart');
      setCart([]);
      navigate('/Dashboard/Orders')
    } else {
      toast.error(data.message)
    }
  }

  useEffect(() => {
    getClientToken();
  }, [auth?.token])

  return (
    <div className='container my-3'>
      <div className="row">
        {!cart.length ? <h1>Cart is Currently Empty</h1> : (
          <>
            <h3 >Hi, {auth.user ? auth.user.name : "YOU"} </h3>
            <p>{cart.length} items in Cart{auth.user && auth.token ? "" : ",  Please Login to Checkout"} </p>

            <div className='col d-flex flex-column gap-3'>
              {
                cart?.map((product) => {
                  return <div className="card p-1 col col-8 d-flex gap-2 p-2" key={product._id}>
                    <div className="row">
                      <img className='mx-0' src={`${process.env.REACT_APP_SERVERURL}/api/products/product-photo/${product._id}`} className="card-img-top mx-auto" alt={product.name}
                        style={{ "width": "6rem", "height": "6rem" }} />

                      <div >
                        <h5 className="card-title">{product.name}</h5>
                        <p className="card-text">
                          {product.description.substring(0, 15)}... </p>
                        <p className="card-text"> Price :
                          {product.price}</p>
                        <button className="btn btn-danger"
                          onClick={() => removeCartItem(product._id)}> Remove </button>
                      </div>
                    </div>
                  </div>
                })
              }
            </div>

            <div className="col col-3">
              <h3>Cart Summary </h3>
              <p>Total | Checkout | Payment </p>
              <hr />
              <p>Total: {totalPrice()}</p>
              <p>Current Address: {auth?.user.address}</p>
              <button className='btn btn-primary'
                onClick={() => navigate('/Dashboard/profile')}>Update Address</button>

              {
                !clientToken  ? "" : (
                  <>
                    <DropIn
                      options={
                        {
                          authorization: clientToken,
                          paypal: {
                            flow: 'vault'
                          }
                        }}
                      onInstance={(instance) => setInstance(instance)}
                    />

                    <button className='btn btn-info mt-3'
                    disabled={!clientToken || !auth?.user.address}  onClick={() => handlePayment()} > Make Payment </button>
                  </>
                )
              }

            </div>
          </>
        )
        }
      </div>
    </div >
  )
}

export default Cart
