import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { toast } from 'react-hot-toast';
import UserMenu from './UserMenu'
import moment from 'moment'

const Orders = () => {
  const [order, setOrder] = useState([]);

  const getOrder = async () => {
    const { data } = await axios({
      method: 'get',
      url: `${process.env.REACT_APP_SERVERURL}/api/paymentIntegration/order`,
      headers: {
        'auth-token': JSON.parse(localStorage.getItem('auth')).token
      }
    })
    if (data.success) {
      console.log(data.order);
      setOrder(data.order)
    } else {
      toast.error(data.message)
    }
  }

  useEffect(() => {
    getOrder();
  }, [])

  return (
    <div className='container mt-5'>
      <div className="row d-flex gap-3">

        <div className="col col-3">
          <UserMenu />
        </div>

        <div className="col col-8" >
          <h1>All Orders</h1>
          {/* {JSON.stringify} */}
          {order?.map((o, i) => {
            return (
              <>
                <table class="table" key={o._id}>
                  <thead>
                    <tr>
                      <th scope="col">#</th>
                      <th scope="col">Status</th>
                      <th scope="col">Buyer</th>
                      <th scope="col"> Date</th>
                      <th scope="col">Payment</th>
                      <th scope="col"> Quantity</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <th scope="row">{i + 1}</th>
                      <td> {o.status}</td>
                      <td className='text-capitalize'> {o.buyer.name}</td>
                      <td> {moment(o.createdAt).fromNow()}</td>
                      <td> {o.payment.success ? "Success" : "Failed"}</td>
                      <td> {o.products.length}</td>
                    </tr>
                  </tbody>
                </table>

                <div className="col">
                  {
                    o?.products?.map((product) => {
                      return <div className="card p-1" key={product._id}>
                        <img src={`${process.env.REACT_APP_SERVERURL}/api/products/product-photo/${product._id}`} className="card-img-top mx-auto" alt={product.name}
                          style={{ "width": "6rem", "height": "6rem" }} />
                        <div className="card-body">
                          <h5 className="card-title">Prouduct: {product.name}</h5>
                          <p className="card-text">
                          Description:   {product.description}</p>
                          <p>Price : {product.price}</p>
                        </div>
                      </div>
                    })
                  }

                </div>
              </>
            )
          })
          }




        </div>

      </div>
    </div>
  )
}

export default Orders
