import React, { useState } from 'react'
import { Link } from 'react-router-dom'

const SearchInput = () => {
    const [searchResult, setSearchResult] = useState({
        keyword:"",
        result: []
    }) 

    return (
        <div>
            <form className="d-flex" role="search" style={{"flexDirection": "row", "width": "100%", "margin": '0rem'}}>
                <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search" value={searchResult.keyword}
                onChange={(e) =>
                 setSearchResult({...searchResult, keyword: e.target.value })} />

                <Link to={`/search/${searchResult.keyword}`}>
                    <button className="btn btn-outline-success" type="submit">Search</button >
                </Link>
            </form>

        </div>
    )
}

export default SearchInput
