import './App.css';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import  { Toaster } from 'react-hot-toast';
import Header from './components/Header';  
import SignUp from './components/Sign Up'; 
import Footer from './components/Footer'; 
import Login from './components/Login';
import UserDashboard from './user/UserDashboard';
import Home from './pages/Home';
import Categories from './pages/Category';
import ForgotPassword from './components/ForgotPassword';
import AdminDashboard from './admin/AdminDashboard';  
import PrivateUserComponents from './user/PrivateUserComponents'; 
import PrivateAdminRoutes from './admin/PrivateAdminRoutes'; 
import Profile from './user/Profile';
import Orders from './user/Orders';
import CreateCategory from './admin/CreateCategory';
import CreateProduct from './admin/CreateProduct';
import Users from './admin/Users';
import Products from './admin/Products';
import SingleProduct from './admin/SingleProduct';
import Search from './components/Search';
import ProductDetails from './components/ProductDetails';
import Category from './pages/Category';
import Cart from './pages/Cart';
import AdminOrders from './admin/AdminOrders';

function App() {
  return (
    <div className="App">

      <Router>
          <Header /> 
         <Routes>

          <Route path='/Dashboard' element={ < PrivateUserComponents />}> 
            <Route path='/Dashboard/user' element={ <UserDashboard/>} />
            <Route path='/Dashboard/Profile' element={ <Profile/> } />
            <Route path='/Dashboard/Orders' element={ <Orders/>} />
          </Route>

          <Route path='/Dashboard' element={ < PrivateAdminRoutes />}> 
            <Route path='/Dashboard/admin' element={ <AdminDashboard/>} /> 
            <Route path='/Dashboard/CreateCategory' element={< CreateCategory />}/>
            <Route path='/Dashboard/CreateProduct' element={< CreateProduct />}/>
            <Route path='/Dashboard/AdminOrders' element={<AdminOrders />}/>
            <Route path='/Dashboard/Products' element={< Products />}/>
            <Route path='/Dashboard/SingleProduct/:slug' element={< SingleProduct  />}/>
          </Route>
          
          <Route path='/' element= { <  Home />} ></Route>
          <Route path='/ProductDetails/:slug' element= { < ProductDetails />} ></Route>
          <Route path='/Category/:slug' element= { <  Category />} ></Route>
          <Route path='/Cart' element= { < Cart />} ></Route>
          <Route path='/Login' element= { <  Login />} ></Route>
          <Route path='/SignUp' element= { <  SignUp/>} ></Route>
          <Route path='/ForgotPassword' element= { < ForgotPassword />} ></Route>
          <Route path='/search/:keyword' element= { < Search />} ></Route>
          <Route path='/*' element= { <h1>4o4 Page not found</h1>} ></Route>

         </Routes>
         <Footer />
         <Toaster />         
      </Router>

    </div>
  );
}

export default App;
