import React, { useContext, useEffect, useState } from 'react'
import axios from 'axios';
import { toast } from 'react-hot-toast';
import { userContext } from '../contex/userContext';
import { useParams, Link, useNavigate } from 'react-router-dom';

const SingleProduct = () => { 
    const [products, setProducts] = useState([]);
    const { auth } = useContext(userContext);
    const params = useParams();
    const navigate = useNavigate();

  const [categories, setCategories] = useState([]); 
  const [name, setName] = useState("");
  const [photo, setPhoto] = useState("");
  const [description, setDescription] = useState("");
  const [price, setPrice] = useState(0);
  const [quantity, setQuantity] = useState(0);
  const [shipping, setShipping] = useState(true);
  const [category, setCategory] = useState("");
  const [id, setId] = useState("");

    const getProduct = async () => {
        const { data } = await axios({
            method: 'get',
            url: `${process.env.REACT_APP_SERVERURL}/api/products/single-product/${params.slug}`,
            headers: {
                'auth-token': auth?.token
            }
        })
        if (data.success) { 
            setName(data.product.name)
            setDescription(data.product.description)
            setPrice(data.product.price)
            setQuantity(data.product.quantity)
            setShipping(data.product.shipping)
            setCategory(data.product.category._id)  
            setId(data.product._id)
        } else {
            toast.error(data.message)
        }
    }

    const getCategories = async () => {
        const { data } = await axios({
          method: 'get',
          url: `${process.env.REACT_APP_SERVERURL}/api/category/all-category`,
          headers: {
            'auth-token': auth?.token
          }
        })
        if (data.success) {
          setCategories(data.categories)
        } else {
          toast.error(data.message)
        }
      }

    const handleUpdateProduct = async (e) => {
        e.preventDefault(); 

        try {
          // As receiving by req.fields in backend so use FormData object
          const productData = new FormData();
          productData.append('name', name);
          productData.append('description', description);
          productData.append('price', price);
          productData.append('quantity', quantity);
          productData.append('shipping', shipping);
          productData.append('category', category);
          photo && productData.append('photo', photo);
    
          const { data } = await axios({
            method: 'put',
            url: `${process.env.REACT_APP_SERVERURL}/api/products/update-product/${id}`,
            data: productData ,
            headers: {
              'auth-token': auth?.token
            }
          }) 
          if (data.success) {
            toast.success(data.message);     
            navigate('/Dashboard/Products'); 
          } else {
            toast.error(data.message)
          }
    
        } catch (error) {
           console.log(error);
           toast.error('Some error occured in catch, Please try again')
        }
      } 

    const  handleDeleteProduct = async (e,id) => {
        e.preventDefault();
        console.log(id);

        const { data } = await axios({
            method: 'delete',
            url: `${process.env.REACT_APP_SERVERURL}/api/products/delete-product/${id}`,
            headers: {
              'auth-token': auth?.token
            }
          })
          if (data.success) {
            navigate('/Dashboard/Products'); 
          } else {
            toast.error(data.message)
          }
    }

    useEffect(() => {
        getProduct();
        getCategories();
    }, [])

    return (
        <div className='container mt-5'>
            <div className="row gap-3">
                <div className="col">
                    <form >
                        <div className="mb-3">
                            <label htmlFor="category" className="form-label">Product Category </label>
                            <select name="category" id="category"
                                value={category}
                                onChange={(e) => setCategory(e.target.value)}>
                                {
                                    categories?.map((category) => {
                                        return <option key={category._id}
                                            value={category._id}>{category.name}</option>
                                    })
                                }
                            </select>

                        </div>
                        <div className="mb-3">
                            <label htmlFor="productphoto" className="form-label">
                                {photo ? photo.name : "Upload Photo"} </label>
                            <input type="file" name="photo" id="photo" value={photo}
                                onChange={(e) => {
                                    return setPhoto(e.target.files[0]) 
                                  } }
                                accept="image/*" />
                        </div>
                        <div className="mb-3">
                            {/* Preview can only made after photo is added so put conditon else error will occur */}
                            {
                                photo ? (
                                    <img src={URL.createObjectURL(photo)} alt={photo.name} height="150px" />
                                )  :
                                <img src={`${process.env.REACT_APP_SERVERURL}/api/products/product-photo/${id}`} 
                                alt={photo.name} height="150px" />                                
                            }
                        </div>
                        <div className="mb-3">
                            <input type="text" className="form-control" placeholder='Name' name='name' value={name}
                                onChange={(e) => setName(e.target.value)} />
                        </div>
                        <div className="mb-3">
                            <textarea className='form-control' name="description" cols="30" rows="5"
                                placeholder='Write a description' value={description}
                                onChange={(e) => setDescription(e.target.value)}></textarea>
                        </div>
                        <div className="mb-3">
                            <input type="number" className="form-control" name='quantity'
                                value={quantity}
                                onChange={(e) => setQuantity(e.target.value)} placeholder='Quantity' />
                        </div>
                        <div className="mb-3">
                            <input type="number" className="form-control" name='price'
                                value={price}
                                onChange={(e) => setPrice(e.target.value)} placeholder='Price' />
                        </div>
                        <div className="mb-3">
                            <label htmlFor="shipping" className="form-label">  Shipping </label>
                            <select name="shipping" id="shipping"
                                value={shipping}
                                onChange={(e) => setShipping(e.target.value)} >
                                <option disabled>Shipping </option>
                                <option value={true}>Yes</option>
                                <option value={false}>No</option>
                            </select>
                        </div>

                        <button type="submit" className="btn btn-primary"
                            onClick={(e) => handleUpdateProduct(e)}> Update </button>
                        <button type="submit" className="btn btn-danger"
                            onClick={(e) => handleDeleteProduct(e,id)}> Delete </button>
                    </form>

                </div>


            </div>

        </div>
    )
}

export default SingleProduct
