import React, { useEffect, useState } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import axios from 'axios';
import { toast } from 'react-hot-toast';
import { Link } from 'react-router-dom'

const ProductDetails = () => {
    const { slug } = useParams();
    const [product, setProduct] = useState({});
    const [similarProducts, setSimilarProducts] = useState([]); 

    const getSimilarProducts = async (pid, cid) => {
        const { data } = await axios({
            method: 'get',
            url: `${process.env.REACT_APP_SERVERURL}/api/products/similar-products/${pid}/${cid}`,
            headers: {
                'auth-token': JSON.parse(localStorage.getItem('auth')).token
            }
        })
        if (data.success) {
            console.log(data.products);
            setSimilarProducts(data.products)
        } else {
            toast.error(data.message)
        }
    }

    const getProduct = async () => {
        const { data } = await axios({
            method: 'get',
            url: `${process.env.REACT_APP_SERVERURL}/api/products/single-product/${slug}`,
            headers: {
                'auth-token': JSON.parse(localStorage.getItem('auth')).token
            }
        })
        if (data.success) {
            setProduct(data.product);
            getSimilarProducts(data.product._id, data.product.category._id);
        } else {
            toast.error(data.message)
        }
    }

    useEffect(() => {
        getProduct();
    }, [])

    return (
        <div className='container my-5'>
            <div className="row gap-3">
                <h1>Product Details</h1>

                <div className="col col-12 my-3">
                    <div className="card p-1" >
                        <img src={`${process.env.REACT_APP_SERVERURL}/api/products/product-photo/${product._id}`} className="card-img-top mx-auto" alt={product.name}
                            style={{ "width": "6rem", "height": "6rem" }} />
                        <div className="card-body">
                            <h5 className="card-title">{product.name}</h5>
                            <p className="card-text">
                                {product.description} </p>
                            <Link className="btn btn-secondary" >Add to Cart  </Link>
                        </div>
                    </div>
                </div>

                <h3>Similar Products</h3>
                <p>{similarProducts.length < 1 && "No Related Products Available."}</p>
                <div className="col d-flex gap-2 flex-wrap ">
                    {
                        similarProducts?.map((product) => {
                            return <div className="card p-1" >
                                <img src={`${process.env.REACT_APP_SERVERURL}/api/products/product-photo/${product._id}`} className="card-img-top mx-auto" alt={product.name}
                                    style={{ "width": "6rem", "height": "6rem" }} />
                                <div className="card-body">
                                    <h5 className="card-title">{product.name}</h5>
                                    <p className="card-text">
                                        {product.description} </p> 
                                    <Link className="btn btn-secondary" >Add to Cart  </Link>
                                </div>
                            </div>
                        })
                    }
                </div>

            </div>

        </div >
    )
}

export default ProductDetails
