import React, { useContext, useEffect, useState } from 'react'
import axios from 'axios';
import { toast } from 'react-hot-toast';
import { Link } from 'react-router-dom'
import { userContext } from '../contex/userContext';
import { prices } from '../components/Price';
import { useCart } from '../contex/cartContex';

const Home = () => {
  const [products, setProducts] = useState([]);
  const [categories, setCategories] = useState([]);

  const { auth } = useContext(userContext);
  const { cart, setCart } = useCart();

  const [total, setTotal] = useState(0);
  const [loading, setLoading] = useState(false);
  const [page, setPage] = useState(2);

  const [radio, setRadio] = useState("");
  const [check, setCheck] = useState([]);
  const [filter, setFilter] = useState(false)

  const getallProducts = async () => {
    const { data } = await axios({
      method: 'get',
      url: `${process.env.REACT_APP_SERVERURL}/api/products/all-products`,
      headers: {
        'auth-token': JSON.parse(localStorage.getItem('auth')).token
      }
    })
    if (data.success) {
      setProducts(data.products) 
    } else {
      toast.error(data.message)
    }
  }

  const getFilterProducts = async () => {
    console.log(radio);
    const { data } = await axios({
      method: 'post',
      url: `${process.env.REACT_APP_SERVERURL}/api/products/products-filter`,
      data: {
        check: check,
        radio: radio
      },
      headers: {
        'auth-token': JSON.parse(localStorage.getItem('auth')).token
      }
    })
    if (data.success) {
      setProducts(data.products)
      console.log(data);
    } else {
      toast.error(data.message)
    }
  }

  const getCategories = async () => {
    const { data } = await axios({
      method: 'get',
      url: `${process.env.REACT_APP_SERVERURL}/api/category/all-category`,
      headers: {
        'auth-token': JSON.parse(localStorage.getItem('auth')).token
      }
    })
    if (data.success) {
      setCategories(data.categories)
    } else {
      toast.error(data.message)
    }
  }

  const handlefiltercategory = (value, id) => {
    let all = [...check]
    console.log('value' + value);
    //  if value=true means if check the box => by onchange
    if (value) {
      all.push(id)
    }  //  if value=false means if uncheck the box => by onchange 
    else {
      all = all.filter(catgid => catgid !== id);
    }
    setCheck(all);
  }

  const productsCount = async () => {
    const { data } = await axios({
      method: 'get',
      url: `${process.env.REACT_APP_SERVERURL}/api/products/products-count`,
      headers: {
        'auth-token': JSON.parse(localStorage.getItem('auth')).token
      }
    })
    if (data.success) {
      setTotal(data.total)
    } else {
      toast.error(data.message)
    }
  }

  const loadMoreProducts = async (e) => {
    e.preventDefault();    
    setPage(page + 1);
    setLoading(true); 

    const { data } = await axios({
      method: 'get',
      url: `${process.env.REACT_APP_SERVERURL}/api/products/products-load/${page}`,
      headers: {
        'auth-token': JSON.parse(localStorage.getItem('auth')).token
      }
    })
    if (data.success) {  
      setProducts([...products, ...data.products]);
      setLoading(false);
    } else {
      toast.error(data.message)
      setLoading(false);
    }
  }

  useEffect(() => {
    if (!check.length > 0 || !radio) getallProducts();
    getCategories();
    productsCount();
  }, [])

  useEffect(() => {
    if (check.length || radio) {
      setFilter(true)
      getFilterProducts();
    }
  }, [check, radio])

  return (
    <div className='container mt-5'>
      <div className="row gap-3">

        <div className="col col-3 d-flex flex-column align-start">
          <h4 style={{ "textAlign": "initial" }}>Filter by Category</h4>
          <div className='categoryfilter mt-3 mb-5'>
            {categories?.map((category) => {
              return <label htmlFor={category._id} className='text-capitalize'>
                <input type="checkbox" id={category._id}
                  onChange={(e) => handlefiltercategory(e.target.checked, category._id)} />  {category.name}
              </label>
            })
            }
          </div>

          <h4 style={{ "textAlign": "initial" }}>Filter by Price</h4>
          <div className='categoryfilter mt-3'>
            {
              prices?.map((price) => {
                return <label htmlFor={price._id} className='text-capitalize'>
                  <input type="radio" id={price._id} value={price.val} name="pricefilter"
                    onChange={(e) => { setRadio(e.target.value) }} />  {price.name}
                </label>
              })
            }
          </div>

          <button className='btn btn-danger w-75 mt-5'
            onClick={() => {
              getallProducts()
              setFilter(false)
              // As above code doesn't ucheck redios and checkboxes
              // window.location.reload();
            }} > RESET FILTERS </button>

        </div>

        <div className="col col-8 ">
          <h1>All Products</h1>
          <div className="d-flex flex-wrap gap-4 mt-4">
            {
              products?.map((product) => {
                return <div className="card p-1" key={product._id}>
                  <img src={`${process.env.REACT_APP_SERVERURL}/api/products/product-photo/${product._id}`} className="card-img-top mx-auto" alt={product.name}
                    style={{ "width": "6rem", "height": "6rem" }} />
                  <div className="card-body">
                    <h5 className="card-title">{product.name}</h5>
                    <p className="card-text">
                      {product.description.substring(0, 15)}... </p>
                    <Link className="btn btn-primary me-1" to={`/ProductDetails/${product.slug}`}>
                      Details </Link>
                    <Link className="btn btn-secondary"
                      onClick={() => {
                        setCart([...cart, product])
                        localStorage.setItem('cart', JSON.stringify([...cart, product]))
                        toast.success('Item added to Cart')
                      }} >Add to Cart  </Link>
                  </div>
                </div>
              })
            }
          </div>

          {products && products.length < total && !filter && <button className='btn btn-warning mt-5'
            onClick={(e) => 
              loadMoreProducts(e)
            }>
            {!loading ? "Load More" : "Loading..."}
          </button>}

        </div>

      </div >

    </div >
  )
}

export default Home
