import React from 'react'
import { Link, useLocation } from 'react-router-dom'

const AdminMenu = () => {
    const location = useLocation(); 

    return (
        <div>
            <ul className="list-group">
                <li className={`${location.pathname == '/Dashboard/CreateCategory' ? "active list-group-item" : "list-group-item" }`}  aria-current="true">
                    <Link to='/Dashboard/CreateCategory'>Create Category</Link>
                </li> 
                <li  className={`${location.pathname == '/Dashboard/CreateProduct' ? "active list-group-item" : "list-group-item" }`} >
                    <Link to='/Dashboard/CreateProduct'>Create Product</Link>
                </li>
                <li className={`${location.pathname == '/Dashboard/Products' ? "active list-group-item" : "list-group-item" }`} >
                    <Link to='/Dashboard/Products'> Products</Link>
                </li>
                <li className={`${location.pathname == '/Dashboard/AdminOrders' ? "active list-group-item" : "list-group-item" }`} >
                    <Link to='/Dashboard/AdminOrders'> Orders </Link>
                </li>
            </ul>
        </div>
    )
}

export default AdminMenu
